'use client';

import { useState } from 'react';
import { useAdminStore } from '../store/adminStore';
import { Pencil } from 'lucide-react';

type Jogador = {
  id: string;
  nome: string;
  pontos: number;
  saldoGols: number;
  vitorias: number;
  derrotas: number;
};

const jogadoresExemplo: Jogador[] = [
  { id: '1', nome: 'Breno', pontos: 12, saldoGols: 5, vitorias: 4, derrotas: 1 },
  { id: '2', nome: 'João', pontos: 9, saldoGols: 2, vitorias: 3, derrotas: 2 },
  { id: '3', nome: 'Carlos', pontos: 6, saldoGols: 0, vitorias: 2, derrotas: 3 },
];

export default function HomePage() {
  const [ordenarPor, setOrdenarPor] = useState<'pontos' | 'saldoGols' | 'vitorias' | 'derrotas'>('pontos');
  const isAdmin = useAdminStore((state) => state.isAdmin);

  // Estado para controlar a exibição do modal
  const [mostrarModal, setMostrarModal] = useState(false);
  const [jogadorEditando, setJogadorEditando] = useState<Jogador | null>(null);

  function handleEditarJogador(jogador: Jogador) {
    setJogadorEditando(jogador);
    setMostrarModal(true);
  }

  const jogadoresOrdenados = [...jogadoresExemplo].sort((a, b) => {
    return b[ordenarPor] - a[ordenarPor];
  });

  return (
    <main className="min-h-screen bg-gray-100 p-6">
      <h1 className="text-3xl font-bold text-center text-blue-700 mb-6">Ranking da Pelada ⚽</h1>

      <div className="flex justify-center gap-4 mb-6 flex-wrap">
        <button onClick={() => setOrdenarPor('pontos')} className={`px-4 py-2 rounded ${ordenarPor === 'pontos' ? 'bg-blue-600 text-white' : 'bg-white text-blue-600 border border-blue-600'}`}>Pontos</button>
        <button onClick={() => setOrdenarPor('saldoGols')} className={`px-4 py-2 rounded ${ordenarPor === 'saldoGols' ? 'bg-blue-600 text-white' : 'bg-white text-blue-600 border border-blue-600'}`}>Saldo de Gol</button>
        <button onClick={() => setOrdenarPor('vitorias')} className={`px-4 py-2 rounded ${ordenarPor === 'vitorias' ? 'bg-blue-600 text-white' : 'bg-white text-blue-600 border border-blue-600'}`}>Vitórias</button>
        <button onClick={() => setOrdenarPor('derrotas')} className={`px-4 py-2 rounded ${ordenarPor === 'derrotas' ? 'bg-blue-600 text-white' : 'bg-white text-blue-600 border border-blue-600'}`}>Derrotas</button>
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full bg-white rounded shadow-md">
          <thead>
            <tr className="bg-blue-600 text-white">
              <th className="py-2 px-4">#</th>
              <th className="py-2 px-4">Nome</th>
              <th className="py-2 px-4">Pontos</th>
              <th className="py-2 px-4">Saldo de Gol</th>
              <th className="py-2 px-4">Vitórias</th>
              <th className="py-2 px-4">Derrotas</th>
            </tr>
          </thead>
          <tbody>
            {jogadoresOrdenados.map((jogador, index) => (
              <tr key={jogador.id} className="text-center border-b">
                <td className="py-2 px-4 font-semibold text-gray-800">{index + 1}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{jogador.nome}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{jogador.pontos}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{jogador.saldoGols}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{jogador.vitorias}</td>
                <td className="py-2 px-4 font-semibold text-gray-800">{jogador.derrotas}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {isAdmin && (
        <div className="fixed bottom-6 right-6">
          <button
            onClick={() => setMostrarModal(true)} // Exibe o modal ao clicar
            className="bg-green-600 text-white px-4 py-2 rounded-full shadow-lg hover:bg-green-700"
          >
            + Jogador
          </button>
        </div>
      )}

      {/* Modal para Adicionar Jogador */}
        {mostrarModal && (
    <div className="fixed inset-0 bg-black bg-opacity-30 flex justify-center items-center z-50">
      <div className="bg-white p-6 rounded shadow-lg w-96">
        <h2 className="py-2 px-4 font-semibold text-gray-800">
          {jogadorEditando ? 'Editar Jogador' : 'Adicionar Jogador'}
        </h2>

        <input
          type="text"
          placeholder="Nome"
          value={jogadorEditando?.nome || ''}
          onChange={(e) =>
            setJogadorEditando((prev) =>
              prev
                ? { ...prev, nome: e.target.value }
                : {
                    id: '',
                    nome: e.target.value,
                    pontos: 0,
                    saldoGols: 0,
                    vitorias: 0,
                    derrotas: 0,
                  }
            )
          }
          className="w-full border rounded px-3 py-2 mb-3 focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder:text-gray-800"
        />

        {/* Inputs adicionais para edição */}
        {jogadorEditando && (
          <>
            <input
              type="number"
              placeholder="Pontos"
              value={jogadorEditando.pontos}
              onChange={(e) =>
                setJogadorEditando((prev) =>
                  prev ? { ...prev, pontos: parseInt(e.target.value) } : null
                )
              }
              className="w-full border rounded px-3 py-2 mb-3 focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder:text-gray-800"
            />
            <input
              type="number"
              placeholder="Saldo de Gols"
              value={jogadorEditando.saldoGols}
              onChange={(e) =>
                setJogadorEditando((prev) =>
                  prev
                    ? { ...prev, saldoGols: parseInt(e.target.value) }
                    : null
                )
              }
              className="w-full border rounded px-3 py-2 mb-3 focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder:text-gray-800"
            />
            <input
              type="number"
              placeholder="Vitórias"
              value={jogadorEditando.vitorias}
              onChange={(e) =>
                setJogadorEditando((prev) =>
                  prev
                    ? { ...prev, vitorias: parseInt(e.target.value) }
                    : null
                )
              }
              className="w-full border rounded px-3 py-2 mb-3 focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder:text-gray-800"
            />
            <input
              type="number"
              placeholder="Derrotas"
              value={jogadorEditando.derrotas}
              onChange={(e) =>
                setJogadorEditando((prev) =>
                  prev
                    ? { ...prev, derrotas: parseInt(e.target.value) }
                    : null
                )
              }
              className="w-full border rounded px-3 py-2 mb-3 focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder:text-gray-800"
            />
          </>
        )}

        <div className="flex justify-end gap-2">
          <button
            onClick={() => {
              setMostrarModal(false);
              setJogadorEditando(null);
            }}
            className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400"
          >
            Cancelar
          </button>
          <button
            onClick={() => {
              if (jogadorEditando) {
                console.log('Salvar edição:', jogadorEditando);
                // Aqui futuramente você pode atualizar a lista ou enviar pro backend
              } else {
                console.log('Adicionar novo jogador (ainda sem lógica)');
              }
              setMostrarModal(false);
              setJogadorEditando(null);
            }}
            className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
          >
            {jogadorEditando ? 'Salvar' : 'Adicionar'}
          </button>
        </div>
      </div>
    </div>
  )}

    </main>
  );
}
